@font-face {
  font-family: bbfont;
  src: url("assets/bbfont.ttf");
}
.App {
  text-align: center;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-link {
  color: #61dafb;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.nav-bar {
  margin-bottom: 20px;
  width: 100%;
  display: flex;
  box-sizing: border-box;
}
.link {
  text-align: center;
  width: 100%;
  text-decoration: none;
  color: white;
  background-color: black;
  padding: 20px 10px;
  box-sizing: border-box;
  transition: all 0.3s;
  cursor: pointer;
  font-family: bbfont;
}
.active {
  background-color: rgb(196, 13, 13);
  font-size: 1.2rem;
}
